import React from 'react';
import { graphql } from 'react-apollo';
import { gql } from 'apollo-boost';
import { CSVLink } from 'react-csv';


const GUEST_LIST = gql`
query {
  guests {
    name,
    guest,
    contact
  }
}
`;

const DownloadGuestlist = graphql(GUEST_LIST)((props) => {
  const returnGuests = props.data.guests.map(value => [...value]);
  const csvData = [
    ['name', 'guest', 'contact'],
    ...returnGuests,
  ];

  return (
    <CSVLink data={csvData} className="btn btn-info mr-1">Download</CSVLink>
  );
});

export default DownloadGuestlist;



